body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


.navbar{
    margin:auto;
    text-align: center;
    max-width: 900px;
}
.all-post{
    max-width: 700px;
    margin: 5% auto;
    background-color: whitesmoke;
    display: flex;
    flex-direction: column;
    overflow-y: scroll;
    height: 70vh;
    padding:1rem;

}
.my-post{
    padding:0.5rem 0;
    margin-top: 0.3rem;
    border: 2px solid black;
}
.my-post img{
    width: 100%;
  height: 100%;
  object-fit: cover;
}
.my-container{
    padding: 1rem;
}
.my-container div{
    display: flex;
    justify-content: left;

}
.my-container img{
    padding-right: 10px;
}
.my-container span,.my-container{
    margin-left: 0.5rem;
    font-size: smaller;
    text-align: left;
}
.my-container h6 {
    margin: 0 0.5rem;
    padding: 0;
    font-weight: bold;
    text-align: left;
}
.my-options{
    text-decoration: none;
        font-size: xx-large;
        padding-right: 20px;
        color: black;

}
.my-nav{
 text-align: center;
    align-items: center;
}
.my-desc{
    padding: 1rem;
}
.my-header{
    display: block;
}
.container img{
    height: 50px;
}
/* .container img .camera{
    height: 30px;
} */
.container div span{
    color: #006238;
    font-size: 2rem;
    position: absolute;
    bottom: 20px;
}
.container ul{
    list-style-type:none;
}
.my-card{
    margin: 10% auto;
  }
  .my-card-wrapper{
    margin-left: 20%;
    text-align: center;
  }
  .my-content{
    max-width: -webkit-fit-content;
    max-width: -moz-fit-content;
    max-width: fit-content;
  }
  .my-title , .my-btn{
    color: #006238;
    font-size: 2rem;
    font-size: Palatino;
    position: relative;
    top: 4rem;
  }
  .my-btn a{
    text-decoration: none;color: #006238;
    font-size: 1.8rem;
    padding: 0 0.5rem;
    margin-top: 1rem;
  }
.my-form{
    max-width: 600px;
    margin: 5% auto;
    background-color: whitesmoke;
    box-sizing: border-box;
    padding: 1rem;
}
.my-input-wrapper{
    display: flex;
    justify-content:space-between;
}
.description-inp-box{
    width: 100%;
}
.my-btn-box{
    text-align: center;
    margin-top: 2rem;
}
.btn{
    background-color: rgb(182 186 191);
    color: grey;
}
